export default {
	name: 'GoodsLists',
	props: {
		goodsLists: Array,
		isList: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			showArr: []
		};
	},
	mounted() {},
	methods: {
		loadImg(index) {
			this.$set(this.showArr, index, true);
		}
	}
};
